import React, { Component } from 'react'
import Dropdown from 'react-dropdown'
import 'react-dropdown/style.css'


class GeneralSearchForm extends Component{

    render() {
        const options = ['ECONOMY', 'PREMIUMECONOMY', 'BUSINESSCLASS', 'FIRSTCLASS'];
        const defaultOption = options[0];
        return(
           <div>
                <div className="form-group">
                    <label>Currency:</label>
                    <input type="text" placeholder="Currency" name="currencyCode" className="form-control" onChange={this.props.onChange}/>
                </div>

                <div className="form-group">
                    <label>Cabin Class:</label>
                    <Dropdown options={options} onChange={this.props.onChange} value={defaultOption} placeholder="Cabin Class"  name="cabinClass"/>
                </div>

                <div className="form-group">
                    <label>Adults:</label>
                    <input type="number" placeholder="Adults" name="adults" className="form-control"  onChange={this.props.onChange}/>
                </div>

                <div className="form-group">
                    <label>Children:</label>
                    <input type="number" placeholder="Children" name="children" className="form-control"  onChange={this.props.onChange}/>
                </div>
            </div>
        );
    }
}
export default GeneralSearchForm;